.mejs-offscreen
   /* Accessibility: hide screen reader texts (and prefer "top" for RTL languages).  Reference: http://blog.rrwd.nl/2015/04/04/the-screen-reader-text-class-why-and-how/
   clip: rect(1px 1px 1px 1px)
   /* IE6, IE7 - no likey commas
   clip: rect(1px, 1px, 1px, 1px)
   /* IE8-IE11 - we likey commas, no support for clip-path
   clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px)
   position: absolute !important
   height: 1px
   width: 1px
   overflow: hidden

.mejs-container
   position: relative
   background: #000
   font-family: "Helvetica", Arial, serif
   text-align: left
   vertical-align: top
   text-indent: 0

.mejs-fill-container
   width: 100%
   height: 100%
   .mejs-container
      width: 100%
      height: 100%
   overflow: hidden

.mejs-container:focus
   outline: none

.me-plugin
   position: absolute

.mejs-embed
   width: 100%
   height: 100%
   margin: 0
   padding: 0
   background: #000
   overflow: hidden
   body
      width: 100%
      height: 100%
      margin: 0
      padding: 0
      background: #000
      overflow: hidden

.mejs-fullscreen
   /* set it to not show scroll bars so 100% will work
   overflow: hidden !important

.mejs-container-fullscreen
   position: fixed
   left: 0
   top: 0
   right: 0
   bottom: 0
   overflow: hidden
   z-index: 1000
   transition: none
   .mejs-mediaelement, video
      width: 100%
      height: 100%

.mejs-clear
   clear: both

/* Start: LAYERS

.mejs-background
   position: absolute
   top: 0
   left: 0

.mejs-mediaelement
   position: absolute
   top: 0
   left: 0
   width: 100%
   height: 100%

.mejs-poster
   position: absolute
   top: 0
   left: 0
   background-size: contain
   background-position: 50% 50%
   background-repeat: no-repeat

\:root .mejs-poster img
   display: none

.mejs-poster img
   border: 0
   padding: 0

.mejs-overlay
   position: absolute
   top: 0
   left: 0
   background: rgba(#32568b, .6)
   transition: .3s ease
   &:hover
      background: transparent

.mejs-overlay-play
   cursor: pointer

.mejs-overlay-button
   position: absolute
   top: 50%
   left: 50%
   width: 100px
   height: 100px
   margin: -50px 0 0 -50px
   background: url('../img/media-element-player/bigplay.svg') no-repeat

.no-svg .mejs-overlay-button
   background-image: url("../img/media-element-player/bigplay.png")

.mejs-overlay:hover .mejs-overlay-button
   background-position: 0 -100px

.mejs-overlay-loading
   position: absolute
   top: 50%
   left: 50%
   width: 80px
   height: 80px
   margin: -40px 0 0 -40px
   background: url("../img/media-element-player/background.png")
   background: #32568b
   border-radius: 50%
   overflow: hidden
   span
      display: block
      width: 80px
      height: 80px
      background: transparent url("../img/media-element-player/loading.gif") 50% 50% no-repeat

/* End: LAYERS

/* Start: CONTROL BAR

.mejs-container .mejs-controls
   position: absolute
   list-style-type: none
   margin: 0
   padding: 0
   bottom: 0
   left: 0
   background: url("../img/media-element-player/background.png")
   background: rgba(#32568b, .7)
   background: linear-gradient(rgba(#32568b, .8), rgba(#32568b, .8))
   height: 30px
   width: 100%
   div
      list-style-type: none
      background-image: none
      display: block
      float: left
      margin: 0
      padding: 0
      width: 26px
      height: 26px
      font-size: 11px
      line-height: 11px
      font-family: "Helvetica", Arial, serif
      border: 0

.mejs-controls .mejs-button button
   cursor: pointer
   display: block
   font-size: 0
   line-height: 0
   text-decoration: none
   margin: 7px 5px
   padding: 0
   position: absolute
   height: 16px
   width: 16px
   border: 0
   background: transparent url("../img/media-element-player/controls.svg") no-repeat

.no-svg .mejs-controls .mejs-button button
   background-image: url("../img/media-element-player/controls.png")

/* :focus for accessibility

.mejs-controls .mejs-button button:focus
   outline: dotted 1px #999

/* End: CONTROL BAR

/* Start: Time (Current / Duration)

.mejs-container .mejs-controls .mejs-time
   color: #fff
   display: block
   height: 17px
   width: auto
   padding: 10px 3px 0 3px
   overflow: hidden
   text-align: center
   -moz-box-sizing: content-box
   -webkit-box-sizing: content-box
   box-sizing: content-box
   font-family: 'Roboto-Regular', Arial, sans-serif
   a
      color: #fff
      font-size: 11px
      line-height: 12px
      display: block
      float: left
      margin: 1px 2px 0 0
      width: auto

/* End: Time (Current / Duration)

